* {
  margin: 0;
  padding: 0;
}
.detail {
  width: 100%;
  height: 100%;
}
.detail .detail-father {
  width: 95%;
  height: 30%;
  margin: 0px auto;
}
.detail .detail-father .detail-title {
  padding-top: 10px;
  margin-bottom: 10px;
  font: bold 25px '黑体';
  color: black;
  text-align: center;
}
.detail .detail-father .author {
  font: bold 16px '黑体';
  text-align: center;
}
.detail .detail-father .comment {
  width: 95%;
  height: 130px;
  margin: auto;
  border: 1px solid gray;
}
.detail .detail-father .comment p {
  text-indent: 2em;
  color: black;
  margin-top: 10px;
  font-size: 18px;
}
.detail h1 {
  font-size: 28px;
  font-weight: bold;
  margin-left: 10px;
  margin-top: 8px;
}
.detail .detail-input {
  padding-top: 20px;
  height: 140px;
  border-top: gray 1px solid;
}
.detail .detail-input .write-content {
  width: 80%;
  margin-left: 5%;
}
.detail .detail-input .button1 {
  position: relative;
  top: -40%;
  left: 3%;
}
.detail .detail-input .button2 {
  top: -20%;
  left: 88%;
}
.detail .detail-sons {
  width: 95%;
  height: 58%;
  overflow: auto;
  margin: auto;
  border-top: grey 1px solid;
}
.detail .detail-sons .h2 {
  color: black;
  font: bold 25px '黑体';
  text-align: center;
  position: relative;
  top: 70px;
}
