.home {
  height: 100%;
  background-color: #f5f5f5;
  background: url(../../static/media/3.780e42cd.jpg) no-repeat;
  background-size: 100% 100%;
}
.home-header {
  display: flex;
  align-items: center;
  height: 100px;
  background-color: rgba(128, 128, 128, 0.5);
}
.home-header img {
  width: 40px;
  height: 40px;
  margin: 0 15px 0 50px;
}
.home-header h1 {
  font-size: 40px;
  color: white;
  margin-left: 50px;
}
.home-content {
  height: 710px;
  width: 60%;
  margin: 50px auto 0px;
  border: 1px black solid;
}
.home-content .home-content-left {
  width: 35%;
  height: 100%;
  border-right: 1px black solid ;
}
.home-content .home-content-left .home-content-left-button {
  background: url(../../static/media/2.d5c67ebd.jpg);
  width: 100%;
  height: 50px;
  font-size: 20px;
  display: block;
}
.home-content .home-content-right {
  width: 65%;
  height: 100%;
  float: right;
}
