.admin {
  width: 100%;
  height: 100%;
  background: url(../../static/media/1.ea74316d.jpg) no-repeat;
  background-size: 100% 100%;
}
.admin .admin-header {
  display: flex;
  align-items: center;
  height: 80px;
  background-color: rgba(21, 20, 13, 0.5);
}
.admin .admin-header img {
  width: 40px;
  height: 40px;
  margin: 0 15px 0 50px;
}
.admin .admin-header h1 {
  font-size: 40px;
  color: white;
  margin-left: 50px;
}
.admin .admin-content {
  width: 400px;
  height: 350px;
  border: 1px green solid;
  margin: 130px auto;
  padding: 20px 40px;
}
.admin .admin-content h2 {
  text-align: center;
  font-size: 30px;
  font-weight: bold;
  margin-bottom: 20px;
}
.admin .admin-content .admin-form .admin-form-button {
  width: 100%;
  margin-top: -10px;
}
