* {
  margin: 0;
  padding: 0;
}
.detail-son {
  width: 100%;
  height: 130px;
  border-bottom: 1px grey solid;
}
.detail-son .author {
  margin-top: 5px;
  font-size: 20px;
  color: black;
}
.detail-son .del {
  height: 30px;
  float: right;
  margin-top: 5px;
  margin-right: 20px;
}
.detail-son .comment {
  height: 60px;
  font-size: 14px;
  color: black;
  margin-top: -10px;
  text-indent: 2em;
  position: relative;
}
.detail-son .date {
  float: right;
  margin-right: 30px;
  position: relative;
  top: 0px;
}
