.write {
  height: 100%;
}
.write .font {
  font: bold 20px '微软雅黑';
  color: black;
}
.write .write-head {
  width: 100%;
  height: 160px;
  background: white;
  border-bottom: 1px solid grey;
}
.write .write-head .write-head-p {
  margin-left: 30px;
  padding-top: 30px;
}
.write .write-head .write-head-p1 {
  float: right;
  font-size: 25px;
  margin-top: 5px;
  margin-right: 30px;
}
.write .write-head .out {
  font: 16px '黑体';
  color: dodgerblue;
  float: right;
  margin-top: -35px;
  margin-right: 45px;
}
.write .write-head .out:hover {
  cursor: pointer;
  text-decoration: underline;
}
.write .write-writting h2 {
  font: bold 25px '黑体';
  color: black;
  text-align: center;
  margin-top: 20px;
}
.write .write-writting .write-title {
  margin-top: 30px;
  text-align: center;
}
.write .write-writting .write-title .write-title-input {
  width: 60%;
}
.write .write-writting .write-content {
  margin-top: 30px;
  text-align: center;
}
.write .write-writting .write-content .write-content-input {
  width: 80%;
}
.write .write-writting .write-content .tips {
  float: right;
  margin-right: 40px;
}
.write .write-writting .write-footer {
  margin-top: 30px;
  text-align: center;
}
.write .write-writting .write-footer .write-button {
  width: 80%;
  height: 40px;
}
.write .write-writting1 h2 {
  font: bold 25px '黑体';
  color: black;
  text-align: center;
  margin-top: 50px;
}
.write .write-writting1 .write-content {
  margin-top: 40px;
  text-align: center;
}
.write .write-writting1 .write-content .write-content-input {
  width: 80%;
  margin-top: 20px;
}
.write .write-writting1 .write-content .tips {
  float: right;
  margin-right: 40px;
}
.write .write-writting1 .write-footer {
  margin-top: 40px;
  text-align: center;
}
.write .write-writting1 .write-footer .write-button {
  width: 80%;
  height: 45px;
}
